// import "../data";
import { useState } from "react";
import "./Card.css";
export default function Card(props) {
  const [like_color, setlike_color] = useState(
    "https://cdn-icons-png.flaticon.com/512/535/535285.png"
  );
  const [click_love, setclick_love] = useState(true);
  function love() {
    setclick_love(!click_love);
    // console.log(click_love);
    if (click_love) {
      setlike_color("https://cdn-icons-png.flaticon.com/512/833/833472.png");
    } else {
      setlike_color("https://cdn-icons-png.flaticon.com/512/535/535285.png");
    }
  }
  return (
    <>
      <div className="card ">
        <img
          className="img"
          src={`https://avatars.dicebear.com/v2/avataaars/${props.user}.svg`}
          alt="error"
        />
        <h3>{props.name}</h3>
        <div className="email flex">
          <img
            className="logo"
            src="https://cdn-icons.flaticon.com/png/512/1865/premium/1865034.png?token=exp=1646393446~hmac=b6d335237849a65d8ed200bcb8329285"
            alt=""
          />
          <p className="p">{props.email}</p>
        </div>
        <div className="contact_number flex">
          <img
            className="logo"
            src="https://cdn-icons-png.flaticon.com/512/88/88277.png"
            alt=""
          />
          <p className="p">{props.phone}</p>
        </div>
        <div className="website flex">
          <img
            className="logo"
            src="https://cdn-icons.flaticon.com/png/512/3178/premium/3178162.png?token=exp=1645969595~hmac=b2f0235f21c66f970cbffbb8342cea51"
            alt="error"
          />
          <p className="p">{props.website}</p>
        </div>
        <div className="button_btm">
          <button className="btn">
            <img
              className="logo"
              src="https://cdn-icons.flaticon.com/png/512/4211/premium/4211495.png?token=exp=1646393591~hmac=0245d60165ff98c1c1ed3afbb46a3d04"
              alt=""
              onClick={() => {
                alert("Edit button not ready");
              }}
            />
          </button>
          |
          <button className="btn btn_like" onClick={love}>
            <img className="logo" src={like_color} alt="" />
          </button>
          |
          <button className="btn btn_delete">
            <img
              className="logo"
              src="https://cdn-icons-png.flaticon.com/512/565/565491.png"
              alt="eror"
              onClick={() => {
                console.log("jdhbbwjh");
                props.deletefun(props.id);
              }}
            />
          </button>
        </div>
      </div>
    </>
  );
}
